import React from "react";
import PropTypes from "prop-types";

export default class ProductCartProductRecensionComponent extends React.Component {
    constructor(props) {
        super(props);
    }

    componentDidMount() {
        console.log("opinion section - mounting component");
    }

    _formatDate(date) {
        return date.toLocaleString();
    }

    render() {
        return (
            this.props.reviews.length > 0 ? (
                <div>
                    {this._renderBody()}
                </div>
            ) : null

        )
    }

    _renderBody(){
        return (
            <div className="opl-cart-product-opinion-section">
                <div className="l-row u-spacing-l u-small-no-spacing-bottom">
                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-6 ">
                      <h2 className="h4 u-small-spacing-m">Oceny i recenzje klient√≥w Orange:</h2>
                      <div className="o-separator u-large-hide u-medium-hide"></div>
                    </div>
                </div>
                <ul className="u-medium-spacing-xl">
                    {this._renderOpinions()}
                </ul>
            </div>
        )
    }

    _renderOpinions(){
       return this.props.reviews.map((review,index) => {
           return (
                <li key={index} className="l-row u-spacing-xl">
                    <div className="l-col l-col-small-12 l-col-medium-3 l-col-2  u-small-spacing">
                        <p className="g-gray4-c">{review.alias}, {review.dateString}</p>
                        <div className="opl-rating"><span className="g-icon g-icon--star g-icon--s"></span><span className="g-icon g-icon--star g-icon--s"></span><span className="g-icon g-icon--star g-icon--s"></span><span className="g-icon g-icon--star g-icon--s"></span><span className="g-icon g-icon--star g-icon--s"></span>
                            <div style={{width: review.percentageRating}} className="opl-rating-bar"><span className="g-icon g-icon--star g-icon--s"></span><span className="g-icon g-icon--star g-icon--s"></span><span className="g-icon g-icon--star g-icon--s"></span><span className="g-icon g-icon--star g-icon--s"></span><span className="g-icon g-icon--star g-icon--s"></span></div>
                        </div>
                    </div>
                    <div className="l-col l-col-small-12 l-col-medium-9 l-col-10 ">
                        <p className="u-spacing">{review.comment}</p>
                    </div>
                </li>
                );
           }, this);
    }
}
